@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900&display=swap%27);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  text-shadow: 0px 0px 10px black;
}

:root {
  --color-1: #011627;
  --color-2: #112240;
  --color-3: #a1e4f1;
  --color-4: #b4e9f4;
  --color-5: #f72585;
}

body {
  background-color: #011627;
  background-color: var(--color-1);
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-family: Montserrat;
}

#root {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

/* fade in classes */
.fade-in {
  opacity: 0;
  transition: opacity 1000ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}

/* flex box classes */
.flex {
  display: flex;
}

.flexColumn {
  display: flex;
  flex-direction: column;
}

.flexJustifyAndAlignCenter {
  justify-content: center;
  align-items: center;
}

.flexAlignCenter {
  align-items: center;
}

.flexJustifyCenter {
  justify-content: center;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #112240;
  background-color: var(--color-2);
}

.HTMLTags {
  font-size: 32px;
  color: #f72585;
  color: var(--color-5);
  font-weight: 400;
  margin: 1vh 0px;
}

.overlay {
  background-color: rgba(0,0,0,0.7);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 20;
}

@media (max-width: 720px) {
  .HTMLTags {
    font-size: 22px;
  }

    ::-webkit-scrollbar {
      width: 0px;
    }
  
}


#firstPage {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  max-height: 1080px;
}

#introContainer {
  width: calc(100% - 10px);
  height: 60%;
  max-height: 648px;
  max-width: 1000px;
  grid-gap: 30px;
  gap: 30px;
}

.introText {
  width: 65%;
  height: 100%;
  justify-content: center;
}

#introContainer h2 {
  margin-left: 30px;
  font-size: 40px;
  color: var(--color-4);
}

#introContainer h3 {
  margin-left: 30px;
  font-size: 44px;
  color: var(--color-3);
}

#introContainer p {
  margin: 20px 0px 5px 30px;
  color: var(--color-3);
  font-size: 26px;
}

.introImageDiv {
  width: 35%;
  height: 100%;
  flex-shrink: 0;
}

.introImageDiv img {
  border-radius: 50%;
  width: 100%;
  aspect-ratio: 1;
  box-shadow: 0px 0px 20px black;
}

@media (max-width: 800px) {
  #firstPage {
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 1px 0px;
  }

  #introContainer {
    position: relative;
    flex-direction: column;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    margin: 80px 0px;
    margin-right: 0px;
  }

  #introContainer h2 {
    font-size: 28px;
    margin: 0px 10px;
  }

  #introContainer h3 {
    font-size: 30px;
    margin: 0px 10px;
  }

  #introContainer p {
    font-size: 18px;
    margin: 10px;
    margin-bottom: 0;
  }

  .introText {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 95%;
    order: 2;
  }

  .introImageDiv {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    order: 1;
  }

  .introImageDiv img {
    width: 80%;
    height: inherit;
  }
}

.socialLinksDiv {
  align-items: center;
  margin: 0 20px 0 auto;
  grid-gap: 20px;
  gap: 20px;
  z-index: 10;
}

.socialLinksDiv h1 {
  position: relative;
  right: 100px;
  color: var(--color-3);
  font-weight: 400;
  font-size: 20px;
}

.socialLinksDiv h1::after {
  position: relative;
  left: 105px;
  bottom: 13px;
  content: "";
  display: block;
  width: 100px;
  height: 4px;
  background-color: var(--color-3);
}

.linkSVG {
  height: 40px;
  width: 40px;
  fill: var(--color-3);
  transition: all 100ms ease-in;
}

.linkSVG:hover {
  fill: var(--color-5);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media (max-width: 900px) {
  .socialLinksDiv {
    display: none;
  }
} 

.jumpLinksDiv {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  z-index: 10;
}

.jumpLinksDiv ol {
  grid-gap: 35px;
  gap: 35px;
  list-style: none;
}

.jumpLinksDiv ol li {
  counter-increment: item 1;
  cursor: pointer;
  transition: color 100ms ease-in;
  font-size: 22px;
}

.jumpLinksDiv ol li a {
  text-decoration: none;
  color: var(--color-3);
  transition: color 100ms ease-in;
  text-shadow: 2px 2px 5px black;
}

.jumpLinksDiv ol li a:hover {
  color: var(--color-5);
}

.jumpLinksDiv ol li::before {
  content: "0" counter(item) ".";
  color: var(--color-5);
  margin-right: 6px;
}

@media (max-width: 900px) {
  .jumpLinksDiv {
    display: none;
  }
}

.navbar {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 80px;
    z-index: 10;
    align-items: center;
    transition: all 200ms ease-in;
}

.navbar img {
    height: 70%;
    position: relative;
    display: none;
}

.navbar__menu {
    margin-left: auto;
    fill: white;
    height: 70%;
    aspect-ratio: 1/1;
    display: none;
    align-items: center;
    justify-content: center;
    transition: all 200ms ease-in;
}

.navbar__menu.active {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    fill: var(--color-5);
}

.menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    z-index: 10;
    grid-gap: 20px;
    gap: 20px;
    -webkit-animation: fade-in 400ms ease-in;
            animation: fade-in 400ms ease-in;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
    } to {
        opacity: 0.98;
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    } to {
        opacity: 0.98;
    }
}

@-webkit-keyframes fade-out {
    from {
        opacity: 0.98;
    } to {
        opacity: 0;
    }
}

@keyframes fade-out {
    from {
        opacity: 0.98;
    } to {
        opacity: 0;
    }
}

.menu__jumps {
    text-decoration: none;
    color: var(--color-2);
    font-weight: 700;
    font-size: 40px;
    transition: all 200ms ease-in;
}

.menu__jumps:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    color: var(--color-5);
}

.menu__socials {
    position: absolute;
    bottom: 10px;
    width: 80%;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.menu__socials svg {
    height: 50px;
    width: 50px;
    fill: var(--color-2);
}

@media (max-width: 900px) {
    .navbar img {
        display: block;
    }

    .navbar__menu {
        display: flex;
    }

    .navbar {
        position: absolute;
        top: 0px;
        height: 55px;
        padding: 0px 10px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
    }
}
#forthPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

.otherSkillsContainer {
  margin: 80px 0vh;
  position: relative;
  width: 95%;
  max-width: 1000px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.otherSkillsDiv {
  position: relative;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
  max-width: 900px;
  height: 400px;
  background-color: var(--color-2);
  overflow: hidden;
  opacity: 0;
  transition: all 500ms ease-in;
  display: flex;
  box-shadow: 0px 0px 20px black;
}

.otherSkillsTextDiv {
  padding: 30px;
  box-sizing: border-box;
  width: calc(100% - 400px);
  grid-gap: 20px;
  gap: 20px;
  height: 100%;
}

.otherSkillsTextDiv h1 {
  color: var(--color-4);
  font-size: 30px;
}

.otherSkillsTextDiv p {
  font-size: 20px;
  color: var(--color-3);
}

.otherSkillsDiv img {
  width: 400px;
  aspect-ratio: 1/1;
}

.otherSkillsDiv iframe {
  position: relative;
  top: -54px;
  width: 400px;
  height: 454px;
  margin-bottom: -54px;
}

.otherSkillsDiv.active {
  opacity: 1;
}

.otherSkillsDiv.active {
  left: 50% !important;
}

.otherSkills__video {
  width: 400px;
  height: 400px;
}

@media (max-width: 1200px) {
  .otherSkillsDiv {
    width: 100%;
  }
}

@media (max-width: 780px) {
  .otherSkillsContainer {
    width: 95%;
  }

  .otherSkillsDiv {
    flex-direction: column;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  
  .otherSkillsTextDiv {
    order: 2;
    width: 100%;
    padding: 15px 10px;
    grid-gap: 5px;
    gap: 5px;
  }

  .otherSkillsTextDiv h1 {
    font-size: 24px;
  }

  .otherSkillsTextDiv p {
    font-size: 16px;
  }

  .otherSkills__video {
    order: 1;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
  }

  .otherSkillsDiv img {
    width: 100%;
  }
}

#secondPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

.projectsContainer {
  margin: 100px 0vh;
  width: 100%;
}

.project-swiper {
  margin: 10px 0px !important;
  overflow: visible !important;
}

.project-swiper .swiper-slide {
  width: 95%;
  max-width: 900px;
  height: 500px !important;
  border-radius: 10px !important;
  overflow: hidden;
  box-shadow: 0px 0px 20px black;
}

.project-cards {
  height: 500px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--color-2);
  transition: opacity 500ms ease-in, -webkit-transform 100ms ease-in;
  transition: transform 100ms ease-in, opacity 500ms ease-in;
  transition: transform 100ms ease-in, opacity 500ms ease-in, -webkit-transform 100ms ease-in;
  padding: 20px 40px;
  text-align: center;
  color: var(--color-4);
  overflow-y: auto;
  border-radius: 10px;
}

.project-cards h1 {
  margin-bottom: 10px;
  color: var(--color-4);
  font-size: 25px;
  transition: color 250ms ease-in;
}

.project-cards li {
  font-size: 18px;
}

.projectSVG {
  position: absolute;
  right: 6vh;
  height: 90%;
  opacity: 0.2;
  z-index: -1;
}

.project-cards::-webkit-scrollbar {
  width: 8px;
}

.project-cards::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: var(--color-3);
}

.project-cards > .title {
  color: var(--color-5);
  margin: 0px 0 10px 0;
  font-size: 30px;
}

.project-cards > p {
  font-size: 18px;
}

.project-cards > .title svg {
  fill: var(--color-4);
  height: 3vh;
  width: 3vh;
  cursor: pointer;
}

.project-cards > .title svg:hover {
  fill: var(--color-5);
}

.project-cards li {
  margin-bottom: 4px;
}

.language-coverage {
  max-width: 500px;
  width: 98%;
  margin: 30px 0;
}

.language-coverage-bar {
  height: 15px;
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  flex-direction: row-reverse;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  margin-bottom: 10px;
  box-shadow: 0px 0px 10px black;
}

.language-coverage-bar div {
  box-shadow: -1px 0px 10px black;
}

.language-coverage-text {
  width: 100%;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 7px;
  row-gap: 7px;
  flex-wrap: wrap;
  color: white;
  font-size: 15px !important;
}

.language-coverage-text div>div {
  height: 22px;
  width: 22px;
  border-radius: 100%;
  margin-right: 7px;
}

@media (max-width: 800px) {
  #projectsContainer {
    margin: 10vh 0px;
    width: 95%;
  }
  
  .project-swiper .swiper-slide {
    width: 95%;
  }

  .project-cards {
    width: 100%;
    padding: 10px;
  }

  .project-cards > .title {
    font-size: 22px;
  }

  .project-cards h1 {
    font-size: 18px;
  }

  .project-cards > p {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    font-size: 14px;
  }

  .project-cards li {
    font-size: 14px;
  }
  
  .language-coverage-text {
    grid-row-gap: 4px;
    row-gap: 4px;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    font-size: 13px !important;
  }
}

#thirdPage {
  position: relative;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100vw;
}

#skillsContainer {
  margin: 10vh 0vh;
  position: relative;
  width: 85%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.skillsProgressBarParentContainer {
  width: 100%;
  box-shadow: 0px 0px 20px black;
  margin: 2vh 0px;
  background-color: var(--color-2);
  ;
  padding: 1px 0px;
  align-items: center;
}

.skillsProgressBarParentContainer h1 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 6.5vh;
  margin: 2vh 0;
  color: var(--color-4);
}

.skillsProgressBarContainer {
  position: relative;
  width: 85%;
  grid-gap: 5%;
  gap: 5%;
  margin: 6vh 0 15vh 0;
}

.skillsProgressBarContainerChildren {
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}

.actualProgressBarParentDiv {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  margin: 0 0 5vh 0;
}

.actualProgressBarParentDiv h2 {
  color: var(--color-4);
  font-size: 3.5vh;
  margin: 0px 2.5vh;
  margin-bottom: 1vh;
}

.bar {
  width: 100%;
  height: 2vh;
  background-color: var(--color-1);
  border-radius: 1vh;
}

.progress {
  height: 2vh;
  background-color: var(--color-5);
  border-radius: 1vh;
  position: absolute;
  bottom: 0px;
  box-shadow: 0px 0px 4px var(--color-5);
}

.progressPercentageDiv {
  position: absolute;
  right: 0px;
  padding: 3px 5px;
  -webkit-transform: translate(1vh, -130%);
          transform: translate(1vh, -130%);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: var(--color-1);
  color: white;
  font-weight: bold;
}

.progressPercentageDiv::after {
  content: "";
  position: absolute;
  display: block;
  height: 1.5vh;
  bottom: -0.725vh;
  right: 1vh;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  aspect-ratio: 1;
  background-color: var(--color-1);
}

@media (max-width: 800px) {
  #skillsContainer {
    width: 95%;
  }

  .skillsProgressBarContainer {
    width: 95%;
    flex-direction: column;
    grid-gap: 0px;
    gap: 0px;
  }

  .skillsProgressBarContainerChildren {
    width: 100%;
  }
}

.work-container {
    margin: 0px auto;
    width: 95%;
    max-width: 1000px;
}

.work-container__main {
    width: 90%;
    margin: 30px auto;
}

.work-container__tabs {
    position: relative;
    flex-shrink: 0;
}

.work-container__tabs .MuiTabs-indicator {
    background-color: var(--color-5);
    transition: all 250ms linear;
    width: 4px;
}

.work-container__tabs__tab {
    color: white !important;
    font-family: Montserrat !important;
    font-weight: 700 !important;
    transition: all 150ms ease-in !important;
    padding: 10px 20px 10px 10px;
    font-size: 18px !important;
    line-height: 18px !important;
    align-items: flex-end !important;
}

.work-container__tabs__tab:hover {
    color: var(--color-5) !important;
}

.work-container__tabs__tab.active {
    color: var(--color-5) !important;
}

.work-container__content {
    color: white;
    padding: 0 10px;
    padding-left: 20px;
}

.work-container__content h1 {
    font-size: 35px;
    color: var(--color-3);
}

.work-container__content p {
    font-size: 18px;
    margin: 5px 0;
    color: var(--color-4);
}

.work-container__content ul {
    list-style: none;
    margin-top: 20px;
}

.work-container__content li {
    position: relative;
    padding: 10px 0 10px 25px;
    font-size: 22px;
}

.work-container__content li::before {
    content: '▹';
    color: var(--color-5);
    position: absolute;
    left: 0;
}

@media (max-width: 1000px) {

    .work-container__main {
        flex-direction: column;
        width: 100%;
        margin: 20px auto;
    }

    .work-container__tabs {
        margin-bottom: 20px !important;
    }

    .work-container__tabs__tab {
        text-align: center;
    }

    .work-container__tabs .MuiTabs-indicator {
        height: 4px;
    }

    .work-container__content {
        padding: 0;
        width: 95%;
        margin: 0 auto;
    }

    .work-container__content h1 {
        font-size: 24px;
    }

    .work-container__content p {
        font-size: 14px;
    }

    .work-container__content li {
        font-size: 16px;
        padding: 10px 0vh 10px 25px;
    }
    
    .work-container__content ul {
        margin-top: 2vh;
    }

    .work-container__tabs__tab {
        font-size: 2vh !important;
    }
}
